    async prepareArbitraryTransaction(accountAddress: string, request: PrepareArbitraryTransactionRequest, queryParams?: PrepareArbitraryTransactionQueryParams): Promise<GridTransactionResponse> {
    try {
      // Validate that we have Grid API client
      if (!this.gridApiClient) {
        throw new ProviderError(
          'Grid API client is required for preparing arbitrary transaction',
          this.providerType
        );
      }

      const transactionSigners = request.transaction_signers || request.account_signers || [];

      request.transaction_signers = transactionSigners; // Api still uses transaction_signers, can be removed when api supports account_signers

      // Call the Grid API to prepare arbitrary transaction
      const response = await this.gridApiClient.prepareArbitraryTransaction(accountAddress, request, queryParams);
      
      if (!response.success || !response.data) {
        throw this.createProviderError(
          response.error || 'Failed to prepare arbitrary transaction',
          response
        );
      }

      return response.data;
    } catch (error: any) {
      if (error instanceof ProviderError) {
        throw error;
      }
      throw new ProviderError(
        `Failed to prepare arbitrary transaction: ${error.message}`,
        this.providerType,
        undefined,
        error,
        error.details
      );
    }
  }

