import { GridApiConfig } from '../api/grid-api-client';

export const DEFAULT_CONFIG = {
  timeout: 10000,
  retryAttempts: 3
};

export function getGridConfig(overrides?: Partial<GridApiConfig>): GridApiConfig {

  const apiKey = overrides?.apiKey;

  if(!apiKey) {
    throw new Error("API key is not set");
  }

  return {
    ...DEFAULT_CONFIG,
    apiKey,
    ...overrides,
    baseUrl: overrides?.baseUrl || "https://grid.squads.xyz",
    environment: overrides?.environment || "sandbox",
    solanaRpcUrl: overrides?.solanaRpcUrl || "https://api.devnet.solana.com",
    timeout: overrides?.timeout || 10000,
    retryAttempts: overrides?.retryAttempts || 3,
  };
}