import canonicalize from 'canonicalize';
// import { cryptoAdapter } from './crypto-adapter';
import { Buffer } from 'buffer';
import { p256 } from '@noble/curves/p256';
  import { hmac } from '@noble/hashes/hmac';
  import { chacha20poly1305 } from '@noble/ciphers/chacha';
import { sha256 } from '@noble/hashes/sha256';


const SUITE_ID = new Uint8Array([/* "HPKE" */72,80,75,69, /* kem */0,16, /* kdf */0,1, /* aead */0,3]);
const HASH_LEN = sha256.outputLen;

const KEM_ID   = 0x0010; // DHKEM(P-256, HKDF-SHA256)
const KDF_ID   = 0x0001; // HKDF-SHA256
const AEAD_ID  = 0x0003; // ChaCha20-Poly1305

function i2osp2(n: number) {
  const b = new Uint8Array(2);
  b[0] = (n >>> 8) & 0xff; b[1] = n & 0xff;
  return b;
}

const KEM_SUITE_ID   = concat(
  new TextEncoder().encode('KEM'),
  i2osp2(KEM_ID)
);

const HPKE_SUITE_ID  = concat(
  new TextEncoder().encode('HPKE'),
  i2osp2(KEM_ID),
  i2osp2(KDF_ID),
  i2osp2(AEAD_ID)
);


function concat(...arrs: Uint8Array[]) {
  const len = arrs.reduce((s,a)=>s+a.length,0);
  const out = new Uint8Array(len);
  let off = 0;
  for (const a of arrs) { out.set(a, off); off += a.length; }
  return out;
}

// HKDF-Extract(salt, ikm) = HMAC-Hash(salt || zeros, ikm)
function hkdfExtract(salt: Uint8Array, ikm: Uint8Array) {
  const _salt = (salt && salt.length) ? salt : new Uint8Array(sha256.outputLen);
  return hmac(sha256, _salt, ikm); // PRK (HashLen bytes)
}

function hkdfExpand(prk: Uint8Array, info: Uint8Array, L: number) {
  const n = Math.ceil(L / HASH_LEN);
  if (n > 255) throw new Error('hkdfExpand: L too large');
  let t: Uint8Array = new Uint8Array(0);
  const okm = new Uint8Array(L);
  let off = 0;

  for (let i = 1; i <= n; i++) {
    const input = new Uint8Array(t.length + info.length + 1);
    input.set(t, 0);
    input.set(info, t.length);
    input[input.length - 1] = i;
    t = hmac(sha256, prk, new Uint8Array(Buffer.from(input))) as Uint8Array;       // T(i) = HMAC(PRK, T(i-1) || info || i)
    const copy = i < n ? HASH_LEN : (L - off);
    okm.set(t.subarray(0, copy), off);
    off += copy;
  }
  return okm;
}

/**
 * Extracts the raw private key from a PKCS#8 DER-encoded private key
 * For P-256 ECDSA keys, the raw private key is 32 bytes
 */
function extractRawPrivateKeyFromPKCS8(pkcs8Buffer: ArrayBuffer): ArrayBuffer {
  const bytes = new Uint8Array(pkcs8Buffer);

  // PKCS#8 private key structure for P-256:
  // The raw 32-byte private key is typically located near the end
  // Look for the 32-byte private key value after the OID sequences

  // For P-256, look for a 32-byte sequence that represents the private key
  // It's usually preceded by an OCTET STRING tag (0x04) and length (0x20 = 32)
  for (let i = 0; i < bytes.length - 33; i++) {
    if (bytes[i] === 0x04 && bytes[i + 1] === 0x20) {
      // Found OCTET STRING with length 32 - this should be our private key
      const rawKey = bytes.slice(i + 2, i + 34);
      if (rawKey.length === 32) {
        return rawKey.buffer.slice(rawKey.byteOffset, rawKey.byteOffset + rawKey.byteLength);
      }
    }
  }

  throw new Error('Could not extract raw private key from PKCS#8 format');
}

// RFC 9180 4: LabeledExtract
function labeledExtract(salt: Uint8Array, suiteId: Uint8Array, label: string, ikm: Uint8Array) {
  const labeledIKM = concat(
    new TextEncoder().encode('HPKE-v1'),
    suiteId,
    new TextEncoder().encode(label),
    ikm
  );
  return hkdfExtract(salt, labeledIKM); // returns PRK
}

// RFC 9180 4: LabeledExpand
function labeledExpand(prk: Uint8Array, suiteId: Uint8Array, label: string, info: Uint8Array, L: number) {
  const labeledInfo = concat(
    i2osp2(L),
    new TextEncoder().encode('HPKE-v1'),
    suiteId,
    new TextEncoder().encode(label),
    info
  );
  return hkdfExpand(prk, labeledInfo, L);   //  Expand-only
}

export async function decryptAuthorizationKey(
  encryptedAuthKey: { encapsulated_key: string; ciphertext: string },
  recipientPrivateKey: string
): Promise<string> {
  // Try the working HPKE implementation without WebCrypto
  try {

    const pkcs8Buffer2 = base64ToBuffer(recipientPrivateKey);
    const rawPrivateKey2 = extractRawPrivateKeyFromPKCS8(pkcs8Buffer2);

    // // 2. Perform ECDH key agreement
    const encapsulatedPublicKey = base64ToBuffer(encryptedAuthKey.encapsulated_key);

    const sharedSecretFull = p256.getSharedSecret(new Uint8Array(rawPrivateKey2), new Uint8Array(encapsulatedPublicKey));

    // 1) Extract x-coordinate (zz) - remove compression byte
    const dhZz = sharedSecretFull.slice(1); // 32B x-coordinate, drop 0x03 compression byte

    // 2) Generate recipient public key (uncompressed)
    const pkRm = p256.getPublicKey(new Uint8Array(rawPrivateKey2), false); // 65B uncompressed, starts with 0x04
    const pkRmCompressed = p256.getPublicKey(new Uint8Array(rawPrivateKey2), true); // 0x02/0x03...

    // 3) Create KEM context = enc || pkRm
    // const kem_context = concat(new Uint8Array(encapsulatedPublicKey), pkRm);
    const kem_context = concat(new Uint8Array(encapsulatedPublicKey), pkRmCompressed);

    // 4) DHKEM derivation per RFC 9180 7.1 (uses KEM_SUITE_ID)
    const eae_prk = labeledExtract(new Uint8Array(), KEM_SUITE_ID, 'eae_prk', dhZz);
    const kem_shared_secret = labeledExpand(eae_prk, KEM_SUITE_ID, 'shared_secret', kem_context, sha256.outputLen);

    // HPKE mode (0x00 = Base mode, no PSK, no Auth)
    const mode = new Uint8Array([0x00]); // base mode
    const psk = new Uint8Array();        // none
    const psk_id = new Uint8Array();     // none
    const info = new Uint8Array();       // none

    // Hash the PSK_ID (empty for base mode) - uses HPKE_SUITE_ID
    const psk_id_hash = labeledExtract(psk, HPKE_SUITE_ID, 'psk_id_hash', psk_id);

    // Hash the info (empty for basic usage) - uses HPKE_SUITE_ID
    const info_hash = labeledExtract(new Uint8Array(), HPKE_SUITE_ID, 'info_hash', info);

    // Create key_schedule_context: mode || psk_id_hash || info_hash
    const key_schedule_context = concat(mode, psk_id_hash, info_hash);

    // Correct: salt = psk (empty), ikm = KEM shared_secret (uses HPKE_SUITE_ID)
    const secret = labeledExtract(psk, HPKE_SUITE_ID, 'secret', kem_shared_secret);

    // LabeledExpand to get key, base_nonce, and exporter_secret (uses HPKE_SUITE_ID)
    const derivedKey = labeledExpand(secret, HPKE_SUITE_ID, 'key', key_schedule_context, 32);
    const baseNonce = labeledExpand(secret, HPKE_SUITE_ID, 'base_nonce', key_schedule_context, 12);

    // 4. Decrypt using ChaCha20Poly1305 with derived baseNonce
    const ciphertext = base64ToBuffer(encryptedAuthKey.ciphertext);
    const encryptedData = new Uint8Array(ciphertext); // Entire ciphertext (no nonce prepended in HPKE)

    const aead = chacha20poly1305(derivedKey, baseNonce);
    const decryptedBytes2 = aead.decrypt(encryptedData);

    return new TextDecoder().decode(decryptedBytes2);


  } catch (error) {
    console.error('WebCrypto HPKE decryption failed:', error);
    throw error;
  }
}

function createPKCS8PrivateKey(privateKeyBytes: Uint8Array): Uint8Array {
      // Simplified PKCS#8 format for P-256 private key
      // For now, just wrap the private key bytes (can be enhanced later)
      const header = new Uint8Array([
        0x30, 0x81, 0x87, // SEQUENCE, length varies
        0x02, 0x01, 0x00, // INTEGER version = 0
        0x30, 0x13, // SEQUENCE algorithm identifier
        0x06, 0x07, 0x2a, 0x86, 0x48, 0xce, 0x3d, 0x02, 0x01, // OID: ecPublicKey
        0x06, 0x08, 0x2a, 0x86, 0x48, 0xce, 0x3d, 0x03, 0x01, 0x07, // OID: prime256v1
        0x04, 0x6d // OCTET STRING
      ]);
      
      // ECPrivateKey structure
      const ecPrivateKey = new Uint8Array([
        0x30, 0x6b, // SEQUENCE
        0x02, 0x01, 0x01, // INTEGER version = 1
        0x04, 0x20, // OCTET STRING, 32 bytes
        ...privateKeyBytes
      ]);
      
      const result = new Uint8Array(header.length + ecPrivateKey.length);
      result.set(header);
      result.set(ecPrivateKey, header.length);
      
      return result;
    }

function createSPKIPublicKey(publicKeyBytes: Uint8Array): Uint8Array {
    // SPKI format for P-256 public key
    // SubjectPublicKeyInfo ::= SEQUENCE {
    //   algorithm         AlgorithmIdentifier,
    //   subjectPublicKey  BIT STRING }
    
    // P-256 algorithm identifier OID: 1.2.840.10045.2.1 (ecPublicKey) + 1.2.840.10045.3.1.7 (prime256v1)
    const algorithmId = new Uint8Array([
      0x30, 0x13, // SEQUENCE, length 19
      0x06, 0x07, 0x2a, 0x86, 0x48, 0xce, 0x3d, 0x02, 0x01, // OID: ecPublicKey
      0x06, 0x08, 0x2a, 0x86, 0x48, 0xce, 0x3d, 0x03, 0x01, 0x07 // OID: prime256v1
    ]);
    
    // BIT STRING wrapper for public key
    const bitStringHeader = new Uint8Array([0x03, publicKeyBytes.length + 1, 0x00]);
    
    // Complete SPKI structure
    const totalLength = algorithmId.length + bitStringHeader.length + publicKeyBytes.length;
    const spki = new Uint8Array(2 + totalLength);
    
    spki[0] = 0x30; // SEQUENCE
    spki[1] = totalLength; // length
    spki.set(algorithmId, 2);
    spki.set(bitStringHeader, 2 + algorithmId.length);
    spki.set(publicKeyBytes, 2 + algorithmId.length + bitStringHeader.length);
    
    return spki;
  }

export async function generateHPKEKeyPair() {
  // Use crypto adapter for cross-platform key generation
  // const keyPair = await cryptoAdapter.generateKeyPair();
  const privateKeyBytes = p256.utils.randomPrivateKey();
  const publicKeyBytes = p256.getPublicKey(privateKeyBytes);
  
  // Manually construct SPKI format for P-256 public key
  const spkiPublicKey = createSPKIPublicKey(publicKeyBytes);
  const pkcs8PrivateKey = createPKCS8PrivateKey(privateKeyBytes);
  
  return {
    publicKey: Buffer.from(spkiPublicKey).toString('base64'),
    privateKey: Buffer.from(pkcs8PrivateKey).toString('base64')
  };
  
}

// Helper function to convert base64 to ArrayBuffer
export function base64ToBuffer(base64: string): ArrayBuffer {
  const buffer = Buffer.from(base64, 'base64');
  return buffer.buffer.slice(buffer.byteOffset, buffer.byteOffset + buffer.byteLength);
}

// Helper function to convert base64 to PEM format
export function toPem(b64: string, label = 'PRIVATE KEY'): string {
  return `-----BEGIN ${label}-----\n${b64.replace(/\s+/g, '').match(/.{1,64}/g)!.join('\n')}\n-----END ${label}-----\n`;
}

// Helper function to extract raw private key from PKCS#8 DER
function extractPrivateKeyFromDER(derBytes: Buffer): Uint8Array {
  // Simple DER parsing to extract 32-byte private key
  // Look for the 32-byte private key in the DER structure
  for (let i = 0; i < derBytes.length - 33; i++) {
    if (derBytes[i] === 0x04 && derBytes[i + 1] === 0x20) {
      return derBytes.slice(i + 2, i + 34);
    }
  }
  throw new Error('Could not extract private key from DER format');
}

// Sign payload with authorization key
export function signPayload(payload: string, authorizationKey: string): string {
  // Parse the KMS payload
  const decodedKmsPayload = Buffer.from(payload, 'base64').toString('utf8');
  const signaturePayload = JSON.parse(decodedKmsPayload);

  // Canonicalize and convert to buffer
  const serializedPayload = canonicalize(signaturePayload) as string;
  const serializedPayloadBuffer = Buffer.from(serializedPayload);

  // Extract raw private key bytes
  let privateKeyBytes: Uint8Array;
  if (authorizationKey.includes('BEGIN PRIVATE KEY')) {
    // Already in PEM format - extract raw key
    const base64Key = authorizationKey.replace(/-----BEGIN PRIVATE KEY-----|\-----END PRIVATE KEY-----|\s/g, '');
    const derBytes = Buffer.from(base64Key, 'base64');
    privateKeyBytes = extractPrivateKeyFromDER(derBytes);
  } else {
    // Assume it's base64 PKCS#8 DER format
    const derBytes = Buffer.from(authorizationKey, 'base64');
    privateKeyBytes = extractPrivateKeyFromDER(derBytes);
  }

  // Hash the payload with SHA256
  const hash = sha256(serializedPayloadBuffer);

  // Sign with P-256 ECDSA
  const signature = p256.sign(hash, privateKeyBytes);

  // Return DER-encoded signature as base64
  const derSignature = signature.toDERRawBytes();
  return Buffer.from(derSignature).toString('base64');
}