  /**
   * Get signers that haven't signed the transaction yet
   */
  private getUnsignedSigners(transaction: string, requestedSigners: string[]): string[] {
    try {
      const transactionBuffer = Buffer.from(transaction, 'base64');
      const versionedTransaction = VersionedTransaction.deserialize(transactionBuffer);

      const accountKeys = versionedTransaction.message.staticAccountKeys;
      const signatures = versionedTransaction.signatures;

      console.log(` Transaction has ${signatures.length} signature slots for ${accountKeys.length} account keys`);

      return requestedSigners.filter(signer => {
        try {
          const signerPubkey = new PublicKey(signer);
          const signerIndex = accountKeys.findIndex(key => key.equals(signerPubkey));

          if (signerIndex === -1) {
            console.log(` Signer ${signer} not found in transaction account keys`);
            return false;
          }

          const signature = signatures[signerIndex];

          // Check if signature is actually present and valid
          const isValidSignature = signature !== null &&
                                  signature.length > 0 &&
                                  !this.isEmptySignature(signature);

          console.log(` Signer ${signer} at index ${signerIndex}: signature ${isValidSignature ? 'present' : 'missing'}`);

          // Return true if signer needs to sign (signature is missing or empty)
          return !isValidSignature;
        } catch (error) {
          // Invalid public key, skip
          console.warn(`Invalid public key in signer list: ${signer}`);
          return false;
        }
      });
    } catch (error) {
      // If we can't deserialize the transaction, return all signers (fallback behavior)
      console.warn('Failed to deserialize transaction for signature checking, falling back to all signers');
      return requestedSigners;
    }
  }

  /**
   * Check if a signature is empty (all zeros or placeholder)
   */
  private isEmptySignature(signature: Uint8Array): boolean {
    // Check if signature is all zeros (64 bytes for Ed25519)
    if (signature.length !== 64) return true;

    return signature.every(byte => byte === 0);
  }

  /**
   * Check if a specific public key has already signed the transaction
   */
  private hasPublicKeySigned(transaction: string, pubkey: string): boolean {
    try {
      const transactionBuffer = Buffer.from(transaction, 'base64');
      const versionedTransaction = VersionedTransaction.deserialize(transactionBuffer);

      const accountKeys = versionedTransaction.message.staticAccountKeys;
      const signatures = versionedTransaction.signatures;

      const signerPubkey = new PublicKey(pubkey);
      const signerIndex = accountKeys.findIndex(key => key.equals(signerPubkey));

      if (signerIndex === -1) return false;

      const signature = signatures[signerIndex];
      const isValidSignature = signature !== null &&
                              signature.length > 0 &&
                              !this.isEmptySignature(signature);

      return isValidSignature;
    } catch (error) {
      return false;
    }
  }

  /**
   * Smart signing with automatic provider selection based on session
   */
  async sign(request: SignRequest): Promise<TransactionResult> {
    // Support both 'payload' and 'transaction' for backward compatibility
    const transaction = request.transactionPayload.transaction;
    const transactionSigners = request.transactionPayload.transaction_signers;
    const kmsPayloads = request.transactionPayload.kms_payloads;
    const { sessionSecrets, session } = request;

    // Validate inputs
    if (!transaction && !kmsPayloads) {
      throw new AuthorizationError('Payload is required for signing', 'MISSING_PAYLOAD');
    }

    if (!sessionSecrets || sessionSecrets.length === 0) {
      throw new AuthorizationError('At least one keypair is required for signing', 'NO_KEYPAIRS');
    }

    const signatures: KmsSingature[] = [];
    let signedTransaction = transaction;

    // Filter out signers that have already signed the transaction
    const unsignedSigners = this.getUnsignedSigners(transaction, transactionSigners);

    console.log(` Signing check: ${transactionSigners.length} total signers, ${unsignedSigners.length} unsigned signers`);
    if (transactionSigners.length > unsignedSigners.length) {
      const alreadySigned = transactionSigners.filter(signer => !unsignedSigners.includes(signer));
      console.log(` Already signed by: ${alreadySigned.join(', ')}`);
    }

    if (unsignedSigners.length === 0) {
      console.log(' All required signers have already signed the transaction');
      return {
        transaction: signedTransaction,
        kms_payloads: signatures
      };
    }

    await Promise.all(unsignedSigners.map(async signer => {
      
      if (kmsPayloads && kmsPayloads.length > 0) {
        const kmsPayload = kmsPayloads.find(kp => kp.address === signer);
        if (kmsPayload) {
          switch (kmsPayload.provider) {
            case 'privy':
              const keypair = sessionSecrets.find(kp => (kp as TaggedKeyPair).provider === 'privy');
              const signature = await this.signWithPrivyProvider(kmsPayload.payload, keypair as TaggedKeyPair, session);
              signatures.push({
                provider: KmsProvider.PRIVY,
                signature
              });
              break;
            case 'external':
              const solanaKeypair = sessionSecrets.find(kp => (kp as Keypair).publicKey.toString() === signer) as Keypair;
              if (!solanaKeypair) {
                throw new AuthorizationError(
                  `No keypair found for external signer: ${signer}`,
                  'KEYPAIR_NOT_FOUND'
                );
              }
              signedTransaction = await this.signWithSolanaProvider(transaction, solanaKeypair);
              break;
            default:
              throw new AuthorizationError(
                `Unsupported provider for signing`,
                'UNSUPPORTED_PROVIDER'
              );
              
          }
        }
      } else {
        const solanaKeypair = sessionSecrets.find(kp => (kp as Keypair).publicKey.toString() === signer) as Keypair;
        if (!solanaKeypair) {
          throw new AuthorizationError(
            `No keypair found for external signer: ${signer}`,
            'KEYPAIR_NOT_FOUND'
          );
        }
        signedTransaction = await this.signWithSolanaProvider(transaction, solanaKeypair);
      }
    }));

    const transactionResult: TransactionResult = {
      transaction: signedTransaction,
      kms_payloads: signatures
    };

    return transactionResult;

    // Auto-detect which keypairs to use based on session and transaction signers
    // const selectedKeypairs = this.selectKeypairsFromSession(keypairs, session, request.transaction_signers);

    // Execute signing in parallel for all selected keypairs
    // const signaturePromises = selectedKeypairs.map(keypair => 
    //   this.signWithProvider(payload, keypair, session)
    // );

    // try {
    //   const signatures = await Promise.all(signaturePromises);
      
    //   // For single signature, return just the signature string for simplicity
    //   if (signatures.length === 1) {
    //     return {
    //       signatures,
    //       payload: signatures[0].signature
    //     };
    //   }
      
    //   return {
    //     signatures,
    //     payload: typeof payload === 'string' ? payload : JSON.stringify(payload)
    //   };
    // } catch (error) {
    //   if (error instanceof ProviderError || error instanceof AuthorizationError) {
    //     throw error;
    //   }
    //   throw new AuthorizationError(
    //     `Failed to sign payload: ${error.message}`,
    //     'SIGNING_FAILED'
    //   );
    // }
  }

