  /**
   * Privy signing implementation using local PrivyProvider
   */
  private async signWithPrivyProvider(
    transaction: any,
    keypair: TaggedKeyPair,
    session: any[] | undefined,
    // timestamp: number
  ): Promise<string> {
    // Check if we have session-based signing available
    if (session && session.length > 0) {
      // Find Privy session
      const privySession = session.find(s => s.session?.Privy);
      if (privySession?.session?.Privy?.session?.encrypted_authorization_key) {
        // Use encrypted authorization key for signing
        const authKey = privySession.session.Privy.session.encrypted_authorization_key;
        const privyRequest: PrivyInternalSignRequest = {
          transaction: typeof transaction === 'string' ? transaction : JSON.stringify(transaction),
          sessionSecrets: [keypair],
          signers: [keypair],
          authorizationKey: authKey
        };

        // Use local PrivyProvider for signing
        const privyProvider = new PrivyProvider({});
        const signature = await privyProvider.sign(privyRequest);
        
        return  signature;
      }
    }

    // Fallback to local signing if no session
    if (!keypair.privateKey) {
      throw new AuthorizationError(
        'Private key required for Privy signing without session',
        'MISSING_PRIVATE_KEY'
      );
    }

    // Use direct signing for fallback
    const signature = signPayload(typeof transaction === 'string' ? transaction : JSON.stringify(transaction), keypair.privateKey);
    
    return signature;
  }

  /**
   * Turnkey signing implementation using local TurnkeyProvider
   */
  private async signWithTurnkeyProvider(
    transaction: any,
    keypair: TaggedKeyPair,
    timestamp: number
  ): Promise<SignatureResult> {
    if (!keypair.privateKey) {
      throw new AuthorizationError(
        'Private key required for Turnkey signing',
        'MISSING_PRIVATE_KEY'
      );
    }

    const turnkeyRequest: TurnkeySignRequest = {
      transaction: typeof transaction === 'string' ? transaction : JSON.stringify(transaction),
      sessionSecrets: [keypair],
      signers: [keypair],
      credentials: '' // TODO: Add actual credentials if needed
    };

    // Use local TurnkeyProvider for signing
    const turnkeyProvider = new TurnkeyProvider({});
    const signature = await turnkeyProvider.sign(turnkeyRequest);
    
    return {
      provider: 'turnkey',
      signature,
      publicKey: keypair.publicKey,
      timestamp
    };
  }

  /**
   * Solana signing implementation
   */
  private async signWithSolanaProvider(
    transaction: string,
    keypair: Keypair
  ): Promise<string> {
    try {
      const transactionBuffer = Buffer.from(transaction, 'base64');
      const versionedTransaction = VersionedTransaction.deserialize(transactionBuffer);
      versionedTransaction.sign([keypair]);
      
      const serializedTransaction = versionedTransaction.serialize();
      return Buffer.from(serializedTransaction).toString('base64');
    } catch (error: any) {
      throw new AuthorizationError(
        `Failed to sign with Solana provider: ${error.message}`,
        'SIGNING_FAILED'
      );
    }
  }

