  /**
   * Select keypairs based on session data
   * Prioritizes signers by role (primary > backup) and matches addresses with authenticated wallets
   */
  private selectKeypairsFromSession(
    keypairs: TaggedKeyPair[],
    session?: any[],
    transaction_signers?: string[]
  ): TaggedKeyPair[] {
    
    // If transaction_signers are provided, use them to determine which providers to use
    if (transaction_signers && transaction_signers.length > 0 && session && session.length > 0) {
      return this.selectKeypairsFromTransactionSigners(keypairs, session, transaction_signers);
    }
    
    // If no session, return all keypairs (for non-session based signing like Solana)
    if (!session || session.length === 0) {
      // Filter for non-session based providers
      const nonSessionKeypairs = keypairs.filter(kp => 
        kp.provider === 'solana' || kp.provider === 'passkey'
      );
      return nonSessionKeypairs.length > 0 ? nonSessionKeypairs : keypairs;
    }

    let selected: TaggedKeyPair[] = [];

    // Check each session entry and match with appropriate keypairs
    for (const sessionEntry of session) {
      
      // Check for Privy session
      if (sessionEntry.provider === 'privy' || sessionEntry.session?.Privy) {
        const privySession = sessionEntry.session?.Privy;
        const authenticatedWallets = privySession?.session?.wallets || [];
        
        
        // Find keypairs that match authenticated Privy wallet addresses
        const matchingPrivyKeypairs = keypairs.filter(kp => {
          if (kp.provider !== 'privy') return false;
          
          // Check if keypair address matches any authenticated wallet address
          const matchesWallet = authenticatedWallets.some((wallet: any) => 
            wallet.address === kp.publicKey || wallet.public_key === kp.publicKey
          );
          
          return matchesWallet;
        });
        
        // Prioritize by role: primary > backup > others
        const sortedPrivyKeypairs = matchingPrivyKeypairs.sort((a, b) => {
          const roleOrder = { primary: 1, backup: 2, solana: 3, passkey: 4 };
          return (roleOrder[a.tag] || 99) - (roleOrder[b.tag] || 99);
        });
        
        if (sortedPrivyKeypairs.length > 0) {
          const selectedPrivyKeypair = sortedPrivyKeypairs[0];
          if (!selected.includes(selectedPrivyKeypair)) {
            selected.push(selectedPrivyKeypair);
          }
        }
      }
      
      // Check for Turnkey session
      if (sessionEntry.provider === 'turnkey' || sessionEntry.session?.Turnkey) {
        const turnkeySession = sessionEntry.session?.Turnkey;
        
        // For Turnkey, we need to match based on provider and available session data
        const matchingTurnkeyKeypairs = keypairs.filter(kp => kp.provider === 'turnkey');
        
        // Prioritize by role: primary > backup > others
        const sortedTurnkeyKeypairs = matchingTurnkeyKeypairs.sort((a, b) => {
          const roleOrder = { primary: 1, backup: 2, solana: 3, passkey: 4 };
          return (roleOrder[a.tag] || 99) - (roleOrder[b.tag] || 99);
        });
        
        if (sortedTurnkeyKeypairs.length > 0) {
          const selectedTurnkeyKeypair = sortedTurnkeyKeypairs[0];
          if (!selected.includes(selectedTurnkeyKeypair)) {
            selected.push(selectedTurnkeyKeypair);
          }
        }
      }
      
      // Check for other providers...
    }

    // If no session-matched keypairs found, check for EOA wallets
    if (selected.length === 0) {
      const eoaKeypairs = keypairs.filter(kp => 
        kp.provider === 'solana' || kp.provider === 'external'
      );
      if (eoaKeypairs.length > 0) {
        selected = eoaKeypairs;
      }
    }

    // Default to primary role keypairs if no selection made
    if (selected.length === 0) {
      const primaryKeypairs = keypairs.filter(kp => kp.tag === 'primary');
      selected = primaryKeypairs.length > 0 ? primaryKeypairs : keypairs;
    }

    return selected;
  }

  /**
   * Select keypairs based on transaction_signers addresses
   * Matches transaction_signers to session wallet addresses to determine providers
   */
  private selectKeypairsFromTransactionSigners(
    keypairs: TaggedKeyPair[],
    session: any[],
    transaction_signers: string[]
  ): TaggedKeyPair[] {
    const selected: TaggedKeyPair[] = [];
    
    // For each required transaction signer
    for (const signerAddress of transaction_signers) {
      
      // Search through all sessions to find which provider has this signer
      let providerFound: SignerProvider | null = null;
      
      for (const sessionEntry of session) {
        // Check Privy session
        if (sessionEntry.provider === 'privy' || sessionEntry.session?.Privy) {
          const privySession = sessionEntry.session?.Privy;
          const authenticatedWallets = privySession?.session?.wallets || [];
          
          const hasThisSigner = authenticatedWallets.some((wallet: any) => 
            wallet.address === signerAddress
          );
          
          if (hasThisSigner) {
            providerFound = 'privy';
            break;
          }
        }
        
      }
      
      if (providerFound) {
        // Find a keypair for this provider
        const matchingKeypairs = keypairs.filter(kp => kp.provider === providerFound);
        
        // Prioritize by role: primary > backup > others
        const sortedKeypairs = matchingKeypairs.sort((a, b) => {
          const roleOrder = { primary: 1, backup: 2, solana: 3, passkey: 4 };
          return (roleOrder[a.tag] || 99) - (roleOrder[b.tag] || 99);
        });
        
        if (sortedKeypairs.length > 0) {
          const selectedKeypair = sortedKeypairs[0];
          
          if (!selected.includes(selectedKeypair)) {
            selected.push(selectedKeypair);
          }
        } else {
          console.log(`  No ${providerFound} keypairs available for signer ${signerAddress}`);
        }
      } else {
        console.log(` Could not find provider for signer ${signerAddress}`);
      }
    }
    
    return selected;
  }

