/////////// Errors ///////////

export interface ErrorDetail {
  field?: string;
  code: string;
  message: string;
  suggestion: string;
  documentation: string;
}

export class AuthorizationError extends Error {
  constructor(
    message: string,
    public readonly code: string,
    public readonly provider?: AuthProvider,
    public readonly cause?: Error,
    public readonly details?: ErrorDetail[],
    public readonly timestamp?: string,
    public readonly requestId?: string
  ) {
    super(message);
    this.name = 'AuthorizationError';
  }

  // Helper method to format error with suggestions
  getFormattedMessage(): string {
    let formatted = this.message;

    if (this.details && this.details.length > 0) {
      const suggestions = this.details
        .filter(detail => detail.suggestion && detail.suggestion.trim())
        .map(detail => detail.suggestion);

      if (suggestions.length > 0) {
        formatted += `\n\nSuggestions:\n${suggestions.map(s => ` ${s}`).join('\n')}`;
      }

      const docs = this.details
        .filter(detail => detail.documentation && detail.documentation.trim())
        .map(detail => detail.documentation);

      if (docs.length > 0) {
        formatted += `\n\nDocumentation: ${docs[0]}`;
      }
    }

    return formatted;
  }
}

export class CustomError extends Error {
  constructor(
    message: string,
    public readonly code: string,
    public readonly cause?: Error
  ) {
    super(message);
    this.name = 'CustomError';
  }
}

export class ProviderError extends Error {
  constructor(
    message: string,
    public readonly provider: AuthProvider,
    public readonly statusCode?: number,
    public readonly cause?: Error,
    public readonly details?: ErrorDetail[],
    public readonly timestamp?: string,
    public readonly requestId?: string
  ) {
    super(message);
    this.name = 'ProviderError';
  }

  // Helper method to format error with suggestions
  getFormattedMessage(): string {
    let formatted = this.message;

    if (this.details && this.details.length > 0) {
      const suggestions = this.details
        .filter(detail => detail.suggestion && detail.suggestion.trim())
        .map(detail => detail.suggestion);

      if (suggestions.length > 0) {
        formatted += `\n\nSuggestions:\n${suggestions.map(s => ` ${s}`).join('\n')}`;
      }

      const docs = this.details
        .filter(detail => detail.documentation && detail.documentation.trim())
        .map(detail => detail.documentation);

      if (docs.length > 0) {
        formatted += `\n\nDocumentation: ${docs[0]}`;
      }
    }

    return formatted;
  }
}

