import { GridApiClient } from '../api/grid-api-client';
import {
  AuthorizationError,
  InitAuthRequest,
  InitAuthResponse,
  CompleteAuthRequest,
  CompleteAuthResponse,
  UniversalKeyPair,
  GridClientUserContext,
  SessionSecrets,
  CompleteAuthRequestWithOtp,
  CompleteAuthAndCreateAccountRequest,
  ProviderError,
  SignerProvider,
  TaggedKeyPair,
  KeyPairTag
} from '../types';
import { PrivyInitAuthRequest, PrivyCompleteAuthRequest } from '../types/privy';
import { TurnkeyInitAuthRequest, TurnkeyCompleteAuthRequest } from '../types/turnkey';
import { ValidationUtils } from '../utils/validation';
import { generateHPKEKeyPair } from '../utils/crypto/privy-crypto';
import { generateSolanaKeyPair } from '../utils/crypto/solana-crypto';
import { generateTurnkeyKeyPair } from '../utils/crypto/turnkey-crypto';
// import { cryptoAdapter } from '../utils/crypto/crypto-adapter';

export interface AuthenticationRequest {
  email?: string;
  keypair?: UniversalKeyPair;
  passkeyId?: string;
  provider?: SignerProvider;
  expiration?: number;
}

export interface AuthenticationContext {
  provider: SignerProvider;
  sessionId?: string;
  authPublicKey?: string;
  email?: string;
  keypair?: UniversalKeyPair;
}

export class AuthenticationManager {
  private gridApiClient: GridApiClient;
  private currentContext?: AuthenticationContext;

  constructor(apiClient: GridApiClient) {
    this.gridApiClient = apiClient;
  }

  /**
   * Smart authentication routing based on request type
   */
  async authenticate(request: AuthenticationRequest): Promise<InitAuthResponse> {
    // Determine authentication type
    const authType = this.determineAuthType(request);
    
    switch (authType) {
      case 'privy':
      case 'turnkey':
        return this.initiateEmailAuth(request.email!, authType, request.expiration);
      
      case 'solana':
        return this.validateEOAWallet(request.keypair!);
      
      case 'passkey':
        return this.initiatePasskeyAuth(request.passkeyId!);
      
      default:
        throw new AuthorizationError(
          `Unsupported authentication type: ${authType}`,
          'UNSUPPORTED_AUTH_TYPE'
        );
    }
  }

  /**
   * Complete authentication based on provider type
   */
  async complete(request: CompleteAuthRequestWithOtp | CompleteAuthAndCreateAccountRequest): Promise<CompleteAuthResponse> {
    // Simplified Privy-only implementation for now
    return this.completePrivyAuth(request);
  }

  /**
   * Complete authentication and create account
   */
  async completeAndCreateAccount(request: CompleteAuthAndCreateAccountRequest): Promise<any> {
    // Simplified Privy-only implementation for now
    return this.completePrivyAuthAndCreateAccount(request);
  }

  /**
   * Generate keypairs for all supported providers with appropriate tagging
   * Returns an array of tagged keypairs for privy (primary), turnkey (backup), solana, and passkey
   */
  async generateSessionSecrets(): Promise<SessionSecrets> {
    const providers: Array<{ type: SignerProvider, tag: KeyPairTag }> = [
      { type: 'privy', tag: 'primary' },
      { type: 'turnkey', tag: 'backup' },
      { type: 'solana', tag: 'solana' },
      { type: 'passkey', tag: 'passkey' }
    ];

    const keyPairs: TaggedKeyPair[] = [];

    for (const { type, tag } of providers) {
      try {
        const keyPair = await this.generateKeyPair(type);
        
        keyPairs.push({
          publicKey: keyPair.publicKey,
          privateKey: keyPair.privateKey,
          provider: type,
          tag: tag
        });
      } catch (error: any) {
        console.warn(` Failed to generate keypair with ${type} provider:`, error.message);
        // Continue with other providers instead of failing completely
      }
    }

    if (keyPairs.length === 0) {
      throw new ProviderError(
        'Failed to generate keypairs with any provider. Please check provider configurations.',
        'privy'
      );
    }

    return keyPairs;
  }

  /**
   * Generate keypair for specific provider
   */
  async generateKeyPair(provider: SignerProvider): Promise<UniversalKeyPair> {
    switch (provider) {
      case 'privy':
        // For now, use HPKE key pair generation
        const hpkeKeyPair = await generateHPKEKeyPair();
        return {
          publicKey: hpkeKeyPair.publicKey,
          privateKey: hpkeKeyPair.privateKey
        };
      
      case 'turnkey':
        return generateTurnkeyKeyPair();
      
      case 'solana':
        const solanaKp = await generateSolanaKeyPair();
        return {
          publicKey: solanaKp.publicKey.toString(),
          privateKey: Buffer.from(solanaKp.secretKey).toString('hex')
        };
      
      case 'passkey':
        const passkeyKp = await generateSolanaKeyPair();
        return {
          publicKey: passkeyKp.publicKey.toString(),
          privateKey: Buffer.from(passkeyKp.secretKey).toString('hex')
        };
      
      default:
        throw new AuthorizationError(
          `Keypair generation not supported for provider: ${provider}`,
          'UNSUPPORTED_PROVIDER'
        );
    }
  }

  // Private methods for each authentication type

  private async initiateEmailAuth(
    email: string, 
    provider: 'privy' | 'turnkey',
    expiration?: number
  ): Promise<InitAuthResponse> {
    // Validate email
    if (!ValidationUtils.isValidEmail(email)) {
      throw new AuthorizationError('Invalid email format', 'INVALID_EMAIL');
    }

    // Generate auth keypair for the provider
    const authKeyPair = await this.generateKeyPair(provider);

    // Store context
    this.currentContext = {
      provider,
      email,
      authPublicKey: authKeyPair.publicKey
    };

    // Call appropriate API method
    if (provider === 'privy') {
      const request: PrivyInitAuthRequest = {
        email,
        expiration
      };
      const response = await this.gridApiClient.initAuthWithPrivy(request);

      if (!response.success) {
        throw new ProviderError(
          response.error || 'Failed to initiate Privy authentication',
          provider,
          response.code ? parseInt(response.code) : undefined
        );
      }
      
      return response.data;
    } else {
      const request: TurnkeyInitAuthRequest = {
        email,
        expiration
      };
      
      const response = await this.gridApiClient.initAuthWithTurnkey(request);
      
      if (!response.success) {
        throw new ProviderError(
          response.error || 'Failed to initiate Turnkey authentication',
          provider,
          response.code ? parseInt(response.code) : undefined
        );
      }
      
      return { data: response.data };
    }
  }

  private async validateEOAWallet(keypair: UniversalKeyPair): Promise<InitAuthResponse> {
    // For EOA wallets, we just validate the keypair
    if (!keypair.publicKey || !keypair.privateKey) {
      throw new AuthorizationError(
        'Invalid keypair: missing public or private key',
        'INVALID_KEYPAIR'
      );
    }

    // Store context
    this.currentContext = {
      provider: 'solana',
      keypair
    };

    // No actual authentication needed for EOA
    return {
      data: {
        authenticated: true,
        provider: 'solana',
        publicKey: keypair.publicKey
      }
    };
  }

  private async initiatePasskeyAuth(passkeyId: string): Promise<InitAuthResponse> {
    throw new AuthorizationError(
      'Passkey authentication not yet implemented',
      'NOT_IMPLEMENTED'
    );
  }

  private async completePrivyAuth(request: CompleteAuthRequestWithOtp | CompleteAuthAndCreateAccountRequest): Promise<CompleteAuthResponse> {
    const { otpCode, user, sessionSecrets } = request as CompleteAuthAndCreateAccountRequest;
    
    // Extract email from context or user
    const email = this.currentContext?.email || user.email;
    if (!email) {
      throw new AuthorizationError('Email is required for Privy authentication', 'MISSING_EMAIL');
    }

    // Find privy keypair
    const privyKeypair = sessionSecrets.find(kp => kp.provider === 'privy');
    if (!privyKeypair) {
      throw new AuthorizationError('Privy keypair not found', 'MISSING_KEYPAIR');
    }

    const privyRequest: PrivyCompleteAuthRequest = {
      email,
      otpCode,
      authPublicKey: privyKeypair.publicKey
    };

    const response = await this.gridApiClient.completePrivyAuth(privyRequest);
    
    if (!response.success) {
      throw new ProviderError(
        response.error || 'Failed to complete Privy authentication',
        'privy',
        response.code ? parseInt(response.code) : undefined
      );
    }

    return response.data;
  }

  private async completeTurnkeyAuth(request: CompleteAuthRequestWithOtp | CompleteAuthAndCreateAccountRequest): Promise<CompleteAuthResponse> {
    const { otpCode, user, sessionSecrets } = request as CompleteAuthAndCreateAccountRequest;
    
    // Extract email from context or user
    const email = this.currentContext?.email || user.email;
    if (!email) {
      throw new AuthorizationError('Email is required for Turnkey authentication', 'MISSING_EMAIL');
    }

    // Find turnkey keypair
    const turnkeyKeypair = sessionSecrets.find(kp => kp.provider === 'turnkey');
    if (!turnkeyKeypair) {
      throw new AuthorizationError('Turnkey keypair not found', 'MISSING_KEYPAIR');
    }

    // TODO: These would need to come from the initAuth response in real implementation
    const turnkeyRequest: TurnkeyCompleteAuthRequest = {
      mpcPrimaryId: 'placeholder_primary_id',
      otpId: 'placeholder_otp_id',
      otpCode: otpCode,
      authPublicKey: turnkeyKeypair.publicKey
    };

    const response = await this.gridApiClient.completeTurnkeyAuth(turnkeyRequest);
    
    if (!response.success) {
      throw new ProviderError(
        response.error || 'Failed to complete Turnkey authentication',
        'turnkey',
        response.code ? parseInt(response.code) : undefined
      );
    }

    return response.data;
  }

  private async completePrivyAuthAndCreateAccount(request: CompleteAuthAndCreateAccountRequest): Promise<any> {
    const { otpCode, user, sessionSecrets } = request;

    const email = this.currentContext?.email || user.email;
    if (!email) {
      throw new AuthorizationError('Email is required', 'MISSING_EMAIL');
    }

    const privyKeypair = sessionSecrets.find(kp => kp.provider === 'privy');
    if (!privyKeypair) {
      throw new AuthorizationError('Privy keypair not found', 'MISSING_KEYPAIR');
    }

    const privyRequest: PrivyCompleteAuthRequest = {
      email,
      otpCode,
      authPublicKey: privyKeypair.publicKey
    };

    const response = await this.gridApiClient.completePrivyAuthAndCreateAccount(privyRequest);
    
    if (!response.success) {
      throw new ProviderError(
        response.error || 'Failed to complete Privy auth and create account',
        'privy',
        response.code ? parseInt(response.code) : undefined
      );
    }

    return response.data;
  }

  private async completeTurnkeyAuthAndCreateAccount(request: CompleteAuthAndCreateAccountRequest): Promise<any> {
    const { otpCode, user, sessionSecrets } = request;
    
    const email = this.currentContext?.email || user.email;
    if (!email) {
      throw new AuthorizationError('Email is required', 'MISSING_EMAIL');
    }

    const turnkeyKeypair = sessionSecrets.find(kp => kp.provider === 'turnkey');
    if (!turnkeyKeypair) {
      throw new AuthorizationError('Turnkey keypair not found', 'MISSING_KEYPAIR');
    }

    // TODO: These would need to come from the initAuth response in real implementation
    const turnkeyRequest: TurnkeyCompleteAuthRequest = {
      mpcPrimaryId: 'placeholder_primary_id',
      otpId: 'placeholder_otp_id',
      otpCode: otpCode,
      authPublicKey: turnkeyKeypair.publicKey
    };

    const response = await this.gridApiClient.completeTurnkeyAuthAndCreateAccount(turnkeyRequest);
    
    if (!response.success) {
      throw new ProviderError(
        response.error || 'Failed to complete Turnkey auth and create account',
        'turnkey',
        response.code ? parseInt(response.code) : undefined
      );
    }

    return response.data;
  }

  private determineAuthType(request: AuthenticationRequest): SignerProvider {
    // Explicit provider
    if (request.provider) {
      return request.provider;
    }

    // Email-based auth (default to privy)
    if (request.email) {
      return 'privy';
    }

    // EOA wallet
    if (request.keypair) {
      return 'solana';
    }

    // Passkey
    if (request.passkeyId) {
      return 'passkey';
    }

    throw new AuthorizationError(
      'Unable to determine authentication type from request',
      'AMBIGUOUS_AUTH_REQUEST'
    );
  }
  // Getters for current context
  getCurrentProvider(): SignerProvider | undefined {
    return this.currentContext?.provider;
  }

  getCurrentContext(): AuthenticationContext | undefined {
    return this.currentContext;
  }

  clearContext(): void {
    this.currentContext = undefined;
  }
}