  private solanaRpcUrl?: string;

  // Context management for user and session state
  private context: GridClientContext;

  // Helper method to create AuthorizationError with complete details
  private createAuthError(
    message: string,
    code: string,
    response?: ApiResponse
  ): AuthorizationError {
    return new AuthorizationError(
      message,
      code,
      undefined,
      undefined,
      response?.details,
      response?.timestamp,
      response?.requestId
    );
  }

  constructor(apiConfig: GridApiConfig) {
    this.apiConfig = apiConfig;
    this.apiClient = new GridApiClient(apiConfig);
    this.authManager = new AuthenticationManager(this.apiClient);
    this.signingManager = new SigningManager(this.apiClient);
    this.solanaRpcUrl = apiConfig.solanaRpcUrl;

    // Initialize empty context with signers array
    this.context = {
      client: {
        environment: this.apiConfig.environment,
        config: this.apiConfig,
        solanaRpcUrl: this.solanaRpcUrl,
      },
      user: {
        signers: [],
      },
    };
  }

  extractSignableTransaction(transactionData: GridTransactionResponse): VersionedTransaction {
    const preparedTxBuffer = Buffer.from(transactionData.data!.transaction, 'base64');
          const preparedTransaction = VersionedTransaction.deserialize(preparedTxBuffer);
  
    return preparedTransaction;
  }

  setExternallySignedTransaction(transactionData: GridTransactionResponse, externallySignedTransaction: VersionedTransaction): GridTransactionResponse {
    return {
        data:{
        transaction: Buffer.from(externallySignedTransaction.serialize()).toString('base64'),
        transaction_signers: transactionData.data!.transaction_signers,
        kms_payloads: transactionData.data!.kms_payloads
      }
    }
  }
  /**
   * Initialize authentication
   * Automatically determines the authentication type based on the request
   */
  async initAuth(
    request: InitAuthRequest | AuthenticationRequest
  ): Promise<InitAuthResponse> {
    try {
      // Support both old and new request formats
      const authRequest: AuthenticationRequest = {
        email: (request as any).email,
        keypair: (request as any).keypair,
        passkeyId: (request as any).passkeyId,
        provider: (request as any).provider,
        expiration: (request as any).expiration,
      };

      const response = await this.authManager.authenticate(authRequest);

      return response;
    } catch (error: any) {
      if (error instanceof AuthorizationError) {
        throw error;
      }
      throw new AuthorizationError(
        `Authentication initialization failed: ${error.message}`,
        "AUTH_INIT_FAILED"
      );
    }
  }

  /**
   * Complete authentication
   */
  async completeAuth(
    request: CompleteAuthRequestWithOtp
  ): Promise<CompleteAuthResponse> {
    try {
      return await this.authManager.complete(request);
    } catch (error: any) {
      if (error instanceof AuthorizationError) {
        throw error;
      }
      throw new AuthorizationError(
        `Authentication completion failed: ${error.message}`,
        "AUTH_COMPLETE_FAILED"
      );
    }
  }

  /**
   * Complete authentication and create account
   */
  async completeAuthAndCreateAccount(
    request: CompleteAuthAndCreateAccountRequest
  ): Promise<any> {
    try {
      const result = await this.authManager.completeAndCreateAccount(request);

      // Update context with account info if available
      if (result?.address) {
        this.context.user.address = result.address;
      }

      return result;
    } catch (error: any) {
      if (error instanceof AuthorizationError) {
        throw error;
      }
      throw new AuthorizationError(
        `Account creation failed: ${error.message}`,
        "ACCOUNT_CREATE_FAILED"
      );
    }
  }

  /**
   * Generate keypairs for all supported providers with appropriate tagging
   * Returns an array of tagged keypairs for privy (primary), turnkey (backup), solana, and passkey
   */
  async generateSessionSecrets(): Promise<SessionSecrets> {
    return await this.authManager.generateSessionSecrets();
  }

  /**
   * Generate keypair for a specific provider
   */
  async generateKeyPair(provider?: SignerProvider): Promise<UniversalKeyPair> {
    // Default to privy if no provider specified
    const targetProvider = provider || "privy";
    return await this.authManager.generateKeyPair(targetProvider);
  }

  /**
   * Sign transaction with automatic provider selection based on session
   *
   * @param request - Contains payload, keypairs, and optional session
   * @returns SignResult with signature(s)
   */
  async sign(request: SignRequest): Promise<TransactionResult> {
    try {
      const result = await this.signingManager.sign(request);
      return result;
    } catch (error: any) {
      if (error instanceof AuthorizationError) {
        throw error;
      }
      throw new AuthorizationError(
        `Signing failed: ${error.message}`,
        "SIGNING_FAILED"
      );
    }
  }

  async signAndSend(request: SignAndSendRequest): Promise<any> {
    try {
      const signRequest: SignRequest = {
        sessionSecrets: request.sessionSecrets,
        transactionPayload: request.transactionPayload,
        session: request.session,
      };
      const transactionResult = await this.signingManager.sign(signRequest);
      const result = await this.send({
        address: request.address,
        signedTransactionPayload: transactionResult,
      });
      return result;
    } catch (error: any) {
      if (error instanceof AuthorizationError) {
        throw error;
      }
      throw new AuthorizationError(
        `Signing failed: ${error.message}`,
        "SIGNING_FAILED"
      );
    }
  }

  /**
   * Send signed transaction to the blockchain
   */
  async send(request: SendTransactionRequest): Promise<any> {
    const { signedTransactionPayload, address } = request;
    const { transaction, kms_payloads } = signedTransactionPayload;

    // Validate required fields
    if (!transaction) {
      throw new AuthorizationError(
        "Transaction is required",
        "MISSING_TRANSACTION"
      );
    }
    if (!address) {
      throw new AuthorizationError("Address is required", "MISSING_ADDRESS");
    }
    // if (!signature || !signature.signatures || signature.signatures.length === 0) {
    //   throw new AuthorizationError('SignResult with signatures is required', 'MISSING_SIGNATURES');
    // }

    // Create KMS payloads with provider information from SignResult
    const submitPayload = {
      transaction,
      kms_payloads,
    };

    const response = await this.apiClient.submitTransaction(
      address,
      submitPayload
    );

    if (!response.success || !response.data) {
      throw this.createAuthError(
        response.error || "Transaction submission failed",
        "SUBMIT_FAILED",
        response
      );
    }

    return response.data;
  }

  /**
   * Create account with simplified input
   */
  async createAccount(
    input: CreateAccountInput | CreateAccountRequest
  ): Promise<CreateAccountResponse> {
    let request: CreateAccountRequest;

    // Check if it's already a full request
    if ("type" in input) {
      request = input as CreateAccountRequest;
    } else {
      // Convert simplified input to full request
      request = this.constructAccountPayload(input as CreateAccountInput);
    }

    const response = await this.apiClient.createAccount(request);

    if (!response.success || !response.data) {
      throw this.createAuthError(
        response.error || "Failed to create account",
        "API_ERROR",
        response
      );
    }

    // Update context with account info
    if (response.data.type === "signers" && response.data.address) {
      this.context.user.address = response.data.address;
    } else if (response.data.type === "email" && response.data.email) {
      // Store email in context for later authentication
      this.context.user.email = response.data.email;
    }

    return response.data;
  }

