  // Payment Methods
  async createPaymentIntent(
    accountAddress: string,
    request: CreatePaymentIntentRequest
  ): Promise<CreatePaymentIntentResponse> {
    if (!accountAddress) {
      throw new AuthorizationError(
        "Account address is required",
        "INVALID_REQUEST"
      );
    }

    const response = await this.apiClient.createPaymentIntent(
      accountAddress,
      request
    );

    if (!response.success || !response.data) {
      throw new AuthorizationError(
        response.error || "Failed to create payment intent",
        "API_ERROR"
      );
    }

    // The API response has the actual data nested under response.data.data
    const apiData = response.data.data || response.data;

    const transactionPayload = {
      kms_payloads: apiData.kms_payloads,
      transaction_signers: apiData.transaction_signers,
      transaction: apiData.transaction,
    };

    // Return response with excluded fields removed and transaction data wrapped
    const {
      kms_payloads,
      transaction_signers,
      transaction,
      ...cleanedResponse
    } = apiData;

    return {
      data: {
        ...cleanedResponse,
        transactionPayload,
      },
    };
  }

