  // Transaction Methods
  async prepareArbitraryTransaction(
    accountAddress: string,
    request: PrepareArbitraryTransactionRequest,
    queryParams?: PrepareArbitraryTransactionQueryParams
  ): Promise<GridTransactionResponse> {
    if (!accountAddress) {
      throw new AuthorizationError(
        "Account address is required",
        "INVALID_REQUEST"
      );
    }

    const response = await this.apiClient.prepareArbitraryTransaction(
      accountAddress,
      request,
      queryParams
    );

    if (!response.success || !response.data) {
      throw this.createAuthError(
        response.error || "Failed to prepare arbitrary transaction",
        "API_ERROR",
        response
      );
    }

    const transactionData = response.data as TransactionPayload;

    if(!transactionData.kms_payloads && !response.data.error) {
      transactionData.kms_payloads = [];
    }

    return {
      data: transactionData,
    };
  }

