  /**
   * Helper to construct account payload from simple input
   */
  private constructAccountPayload(
    input: CreateAccountInput
  ): CreateAccountRequest {
    if (!input.email && !input.signer) {
      throw new AuthorizationError(
        "Must provide either email or signer",
        "INVALID_REQUEST"
      );
    }

    if (input.email && input.signer) {
      throw new AuthorizationError(
        "Cannot provide both email and signer",
        "AMBIGUOUS_REQUEST"
      );
    }

    if (input.email) {
      if (!ValidationUtils.isValidEmail(input.email)) {
        throw new AuthorizationError("Invalid email format", "INVALID_EMAIL");
      }

      return {
        type: "email",
        email: input.email,
      } as EmailAccountRequest;
    }

    if (input.signer) {
      if (input.signer.length < 32 || input.signer.length > 44) {
        throw new AuthorizationError(
          "Invalid signer address format",
          "INVALID_REQUEST"
        );
      }

      const accountPolicies: AccountPolicies = {
        signers: [
          {
            address: input.signer,
            role: "primary",
            permissions: ["CAN_INITIATE", "CAN_EXECUTE", "CAN_VOTE"],
            provider: "external",
          },
        ],
        threshold: 1,
        time_lock: null,
        admin_address: null,
      };

      return {
        type: "signers",
        policies: accountPolicies,
        grid_user_id: null,
        memo: undefined,
      } as SignersAccountRequest;
    }

    throw new AuthorizationError("Invalid input parameters", "INVALID_REQUEST");
  }
