import { PublicKey } from '@solana/web3.js';
import { AuthProviderOptions, TurnkeyConfig, PasskeyConfig, SolanaConfig, ProviderConfig, AuthorizationError } from '../types';

export class ValidationUtils {
  static validateProviderOptions(options: AuthProviderOptions): void {
    if (!options) {
      throw new AuthorizationError('Provider options are required', 'INVALID_OPTIONS');
    }

    if (!options.provider) {
      throw new AuthorizationError('Provider type is required', 'MISSING_PROVIDER_TYPE');
    }

    // Config is now optional - only validate if provided
    if (options.config) {
      switch (options.provider) {
        case 'turnkey':
          this.validateTurnkeyConfig(options.config as TurnkeyConfig);
          break;
        case 'passkey':
          this.validatePasskeyConfig(options.config as PasskeyConfig);
          break;
        case 'solana':
          this.validateSolanaConfig(options.config as SolanaConfig);
          break;
        default:
          throw new AuthorizationError(
            `Unknown provider type: ${options.provider}`,
            'UNKNOWN_PROVIDER',
            options.provider
          );
      }
    }
  }

  static validateTurnkeyConfig(config: TurnkeyConfig): void {
    // TurnkeyConfig now only uses base ProviderConfig fields
    this.validateBaseProviderConfig(config);
  }

  static validateBaseProviderConfig(config: ProviderConfig): void {
    if (config.timeout && (config.timeout < 1000 || config.timeout > 300000)) {
      throw new AuthorizationError('Timeout must be between 1000ms and 300000ms', 'INVALID_TIMEOUT');
    }

  }

  static validatePasskeyConfig(config: PasskeyConfig): void {
    // PasskeyConfig now only uses base ProviderConfig fields
    this.validateBaseProviderConfig(config);
  }

  static validateSolanaConfig(config: SolanaConfig): void {
    // SolanaConfig now only uses base ProviderConfig fields
    this.validateBaseProviderConfig(config);
  }

  static validateAuthorizationIntent(intent: any): void {
    if (!intent) {
      throw new AuthorizationError('Authorization intent is required', 'MISSING_INTENT');
    }

    if (!intent.address) {
      throw new AuthorizationError('Smart account address is required', 'MISSING_SMART_ACCOUNT');
    }

    try {
      new PublicKey(intent.address);
    } catch (error) {
      throw new AuthorizationError('Invalid smart account address', 'INVALID_SMART_ACCOUNT');
    }

    if (!intent.operation) {
      throw new AuthorizationError('Operation is required', 'MISSING_OPERATION');
    }

    this.validateSmartAccountOperation(intent.operation);

    if (intent.signers) {
      intent.signers.forEach((signer: any, index: number) => {
        try {
          new PublicKey(signer);
        } catch (error) {
          throw new AuthorizationError(`Invalid signer address at index ${index}`, 'INVALID_SIGNER');
        }
      });
    }
  }

  private static validateSmartAccountOperation(operation: any): void {
    if (!operation.type) {
      throw new AuthorizationError('Operation type is required', 'MISSING_OPERATION_TYPE');
    }

    const validTypes = ['transfer', 'swap', 'stake', 'custom'];
    if (!validTypes.includes(operation.type)) {
      throw new AuthorizationError(
        `Invalid operation type. Must be one of: ${validTypes.join(', ')}`,
        'INVALID_OPERATION_TYPE'
      );
    }

    if (operation.type === 'transfer') {
      if (!operation.amount) {
        throw new AuthorizationError('Transfer amount is required', 'MISSING_TRANSFER_AMOUNT');
      }

      if (!operation.recipient) {
        throw new AuthorizationError('Transfer recipient is required', 'MISSING_TRANSFER_RECIPIENT');
      }

      try {
        new PublicKey(operation.recipient);
      } catch (error) {
        throw new AuthorizationError('Invalid transfer recipient address', 'INVALID_TRANSFER_RECIPIENT');
      }

      if (operation.token) {
        try {
          new PublicKey(operation.token);
        } catch (error) {
          throw new AuthorizationError('Invalid token address', 'INVALID_TOKEN_ADDRESS');
        }
      }
    }
  }

  static isValidPublicKey(publicKey: any): boolean {
    try {
      new PublicKey(publicKey);
      return true;
    } catch {
      return false;
    }
  }

  static isValidEmail(email: string): boolean {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  }

  static sanitizeConfig(config: any): any {
    const sanitized = { ...config };
    
    // Remove sensitive fields from logs
    if (sanitized.privateKey) {
      sanitized.privateKey = '***REDACTED***';
    }
    if (sanitized.mnemonic) {
      sanitized.mnemonic = '***REDACTED***';
    }
    if (sanitized.clientSecret) {
      sanitized.clientSecret = '***REDACTED***';
    }
    
    return sanitized;
  }
}