import { p256 } from "@noble/curves/p256";
import * as hkdf from "@noble/hashes/hkdf";
import { sha256 } from "@noble/hashes/sha256";
import { chacha20poly1305 } from "@noble/ciphers/chacha";
import { randomBytes } from "@noble/hashes/utils";

import {
  uint8ArrayToHexString,
  uint8ArrayFromHexString,
} from "@turnkey/encoding";

// Math functions (from ./math.ts)
/**
 * Test if a specific bit is set.
 */
const testBit = (n: bigint, i: number): boolean => {
  const m = BigInt(1) << BigInt(i);
  return (n & m) !== BigInt(0);
};

/**
 * Compute the modular exponentiation.
 */
const modPow = (b: bigint, exp: bigint, p: bigint): bigint => {
  if (exp === BigInt(0)) {
    return BigInt(1);
  }
  let result = b % p;
  const exponentBitString = exp.toString(2);
  for (let i = 1; i < exponentBitString.length; ++i) {
    result = (result * result) % p;
    if (exponentBitString[i] === "1") {
      result = (result * b) % p;
    }
  }
  return result;
};

/**
 * Compute the modular square root using the Tonelli-Shanks algorithm.
 */
const modSqrt = (x: bigint, p: bigint): bigint => {
  if (p <= BigInt(0)) {
    throw new Error("p must be positive");
  }
  const base = x % p;

  // Check if p % 4 == 3 (applies to NIST curves P-256, P-384, and P-521)
  if (testBit(p, 0) && testBit(p, 1)) {
    const q = (p + BigInt(1)) >> BigInt(2);
    const squareRoot = modPow(base, q, p);
    if ((squareRoot * squareRoot) % p !== base) {
      throw new Error("could not find a modular square root");
    }
    return squareRoot;
  }

  // Other elliptic curve types not supported
  throw new Error("unsupported modulus value");
};

// HPKE Constants (from Turnkey implementation)
const HPKE_VERSION = new Uint8Array([72, 80, 75, 69, 45, 118, 49]); // "HPKE-v1"

// ChaCha20-Poly1305 specific constants
const CHACHA_KEY_INFO = new Uint8Array([
  0,
  32, // Length: 32 bytes
  72,
  80,
  75,
  69,
  45,
  118,
  49, // "HPKE-v1"
  0,
  16, // KEM ID: DHKEM-P256-HKDF-SHA256 (0x0010)
  0,
  1, // KDF ID: HKDF-SHA256 (0x0001)
  0,
  3, // AEAD ID: ChaCha20Poly1305 (0x0003)
  107,
  101,
  121, // "key"
]);

const NONCE_INFO = new Uint8Array([
  0,
  12, // Length: 12 bytes
  72,
  80,
  75,
  69,
  45,
  118,
  49, // "HPKE-v1"
  0,
  16, // KEM ID: DHKEM-P256-HKDF-SHA256 (0x0010)
  0,
  1, // KDF ID: HKDF-SHA256 (0x0001)
  0,
  3, // AEAD ID: ChaCha20Poly1305 (0x0003)
  98,
  97,
  115,
  101,
  95,
  110,
  111,
  110,
  99,
  101, // "base_nonce"
]);

// Suite IDs (from Turnkey implementation)
const SUITE_ID_1 = new Uint8Array([
  75,
  69,
  77, // "KEM"
  0,
  16, // DHKEM-P256-HKDF-SHA256
]);

const SUITE_ID_2 = new Uint8Array([
  72,
  80,
  75,
  69, // "HPKE"
  0,
  16, // KEM ID: DHKEM-P256-HKDF-SHA256
  0,
  1, // KDF ID: HKDF-SHA256
  0,
  3, // AEAD ID: ChaCha20Poly1305
]);

// Labels (from Turnkey implementation)
const LABEL_EAE_PRK = new Uint8Array([
  101,
  97,
  101,
  95,
  112,
  114,
  107, // "eae_prk"
]);

const LABEL_SHARED_SECRET = new Uint8Array([
  115,
  104,
  97,
  114,
  101,
  100,
  95,
  115,
  101,
  99,
  114,
  101,
  116, // "shared_secret"
]);

const LABEL_SECRET = new Uint8Array([
  115,
  101,
  99,
  114,
  101,
  116, // "secret"
]);

const LABEL_BASE_NONCE = new Uint8Array([
  98,
  97,
  115,
  101,
  95,
  110,
  111,
  110,
  99,
  101, // "base_nonce"
]);

interface HpkeDecryptParams {
  ciphertextBuf: Uint8Array;
  encappedKeyBuf: Uint8Array;
  receiverPriv: string;
}

interface HpkeEncryptParams {
  plainTextBuf: Uint8Array;
  targetKeyBuf: Uint8Array;
}

interface HpkeAuthEncryptParams {
  plainTextBuf: Uint8Array;
  targetKeyBuf: Uint8Array;
  senderPriv: string;
}

interface KeyPair {
  privateKey: string;
  publicKey: string;
  publicKeyUncompressed: string;
}

/**
 * Get PublicKey function
 * Derives public key from Uint8Array or hexstring private key
 */
export const getPublicKey = (
  privateKey: Uint8Array | string,
  isCompressed: boolean = true
): Uint8Array => {
  return p256.getPublicKey(privateKey, isCompressed);
};

/**
 * HPKE Encrypt Function with ChaCha20-Poly1305
 * Based on Turnkey's implementation but using ChaCha20-Poly1305 instead of AES-GCM
 */
export const hpkeChachaEncrypt = ({
  plainTextBuf,
  targetKeyBuf,
}: HpkeEncryptParams): Uint8Array => {
  try {
    // Standard HPKE Mode (Ephemeral Key Pair)
    const ephemeralKeyPair = generateP256KeyPair();
    const senderPrivBuf = uint8ArrayFromHexString(ephemeralKeyPair.privateKey);
    const senderPubBuf = uint8ArrayFromHexString(
      ephemeralKeyPair.publicKeyUncompressed
    );

    const aad = buildAdditionalAssociatedData(senderPubBuf, targetKeyBuf);

    // Step 1: Generate Shared Secret
    const ss = deriveSS(targetKeyBuf, uint8ArrayToHexString(senderPrivBuf!));

    // Step 2: Generate the KEM context
    const kemContext = getKemContext(
      senderPubBuf,
      uint8ArrayToHexString(targetKeyBuf)
    );

    // Step 3: Build the HKDF inputs for key derivation
    let ikm = buildLabeledIkm(LABEL_EAE_PRK, ss, SUITE_ID_1);
    let info = buildLabeledInfo(
      LABEL_SHARED_SECRET,
      kemContext,
      SUITE_ID_1,
      32
    );
    const sharedSecret = extractAndExpand(new Uint8Array([]), ikm, info, 32);

    // Step 4: Derive the ChaCha20 key
    ikm = buildLabeledIkm(LABEL_SECRET, new Uint8Array([]), SUITE_ID_2);
    info = CHACHA_KEY_INFO;
    const key = extractAndExpand(sharedSecret, ikm, info, 32);

    // Step 5: Derive the nonce
    info = NONCE_INFO;
    const nonce = extractAndExpand(sharedSecret, ikm, info, 12);

    // Step 6: Encrypt the data using ChaCha20-Poly1305
    const encryptedData = chachaEncrypt(plainTextBuf, key, nonce, aad);

    // Step 7: Concatenate the encapsulated key and the encrypted data for output
    const compressedSenderBuf = compressRawPublicKey(senderPubBuf);
    const result = new Uint8Array(
      compressedSenderBuf.length + encryptedData.length
    );
    result.set(compressedSenderBuf, 0);
    result.set(encryptedData, compressedSenderBuf.length);

    return result;
  } catch (error) {
    throw new Error(`Unable to perform hpkeChachaEncrypt: ${error}`);
  }
};

/**
 * HPKE Authenticated Encrypt Function with ChaCha20-Poly1305
 * Based on Turnkey's implementation but using ChaCha20-Poly1305 instead of AES-GCM
 */
export const hpkeChachaAuthEncrypt = ({
  plainTextBuf,
  targetKeyBuf,
  senderPriv,
}: HpkeAuthEncryptParams): Uint8Array => {
  try {
    // Authenticated HPKE Mode
    const senderPrivBuf = uint8ArrayFromHexString(senderPriv);
    const senderPubBuf = getPublicKey(senderPriv, false);

    const aad = buildAdditionalAssociatedData(senderPubBuf, targetKeyBuf);

    // Step 1: Generate Shared Secret
    const ss = deriveSS(targetKeyBuf, uint8ArrayToHexString(senderPrivBuf!));

    // Step 2: Generate the KEM context
    const kemContext = getKemContext(
      senderPubBuf,
      uint8ArrayToHexString(targetKeyBuf)
    );

    // Step 3: Build the HKDF inputs for key derivation
    let ikm = buildLabeledIkm(LABEL_EAE_PRK, ss, SUITE_ID_1);
    let info = buildLabeledInfo(
      LABEL_SHARED_SECRET,
      kemContext,
      SUITE_ID_1,
      32
    );
    const sharedSecret = extractAndExpand(new Uint8Array([]), ikm, info, 32);

    // Step 4: Derive the ChaCha20 key
    ikm = buildLabeledIkm(LABEL_SECRET, new Uint8Array([]), SUITE_ID_2);
    info = CHACHA_KEY_INFO;
    const key = extractAndExpand(sharedSecret, ikm, info, 32);

    // Step 5: Derive the nonce
    info = NONCE_INFO;
    const nonce = extractAndExpand(sharedSecret, ikm, info, 12);

    // Step 6: Encrypt the data using ChaCha20-Poly1305
    const encryptedData = chachaEncrypt(plainTextBuf, key, nonce, aad);

    // Step 7: Concatenate the encapsulated key and the encrypted data for output
    const compressedSenderBuf = compressRawPublicKey(senderPubBuf);
    const result = new Uint8Array(
      compressedSenderBuf.length + encryptedData.length
    );
    result.set(compressedSenderBuf, 0);
    result.set(encryptedData, compressedSenderBuf.length);
    return result;
  } catch (error) {
    throw new Error(`Unable to perform hpkeChachaAuthEncrypt: ${error}`);
  }
};

/**
 * HPKE Decrypt Function with ChaCha20-Poly1305
 * Based on Turnkey's implementation but using ChaCha20-Poly1305 instead of AES-GCM
 */
export const hpkeChachaDecrypt = async ({
  ciphertextBuf,
  encappedKeyBuf,
  receiverPriv,
}: HpkeDecryptParams): Promise<Uint8Array> => {
  try {
    // Step 1: DHKEM decap - Generate DH shared secret
    const ss = deriveSS(encappedKeyBuf, receiverPriv);

    // Step 2: Generate KEM context = enc || pkR(serialized)
    // @hpke/core: kemContext = concat(new Uint8Array(enc), new Uint8Array(pkrm));
    const receiverPubBuf = getPublicKey(
      uint8ArrayFromHexString(receiverPriv),
      false
    );
    const kemContext = new Uint8Array(
      encappedKeyBuf.length + receiverPubBuf.length
    );
    kemContext.set(encappedKeyBuf, 0);
    kemContext.set(receiverPubBuf, encappedKeyBuf.length);

    // Step 3: DHKEM _generateSharedSecret - exact @hpke/core implementation
    // labeledIkm = kdf.buildLabeledIkm(LABEL_EAE_PRK, dh)
    const labeledIkm = buildHpkeCoreLabeledIkm(LABEL_EAE_PRK, ss, SUITE_ID_1);
    // labeledInfo = kdf.buildLabeledInfo(LABEL_SHARED_SECRET, kemContext, secretSize=32)
    const labeledInfo = buildHpkeCoreLabeledInfo(
      LABEL_SHARED_SECRET,
      kemContext,
      SUITE_ID_1,
      32
    );
    // extractAndExpand(EMPTY.buffer, labeledIkm.buffer, labeledInfo.buffer, secretSize)
    const kemSharedSecret = extractAndExpand(
      new Uint8Array([]),
      labeledIkm,
      labeledInfo,
      32
    );
    
    const mode = new Uint8Array([0x00]); // Base mode
    const psk = new Uint8Array(); // Empty PSK
    const pskId = new Uint8Array(); // Empty PSK ID
    const info = new Uint8Array(); // Empty info

    // PSK ID hash using @hpke/core method - labeledExtract, NOT extractAndExpand
    const pskIdHash = await webCryptoLabeledExtract(
      new ArrayBuffer(0), // empty salt (EMPTY.buffer)
      new Uint8Array([112, 115, 107, 95, 105, 100, 95, 104, 97, 115, 104]), // "psk_id_hash"
      pskId,
      SUITE_ID_2
    );

    // Info hash using @hpke/core method - labeledExtract, NOT extractAndExpand
    const infoHash = await webCryptoLabeledExtract(
      new ArrayBuffer(0), // empty salt (EMPTY.buffer)
      new Uint8Array([105, 110, 102, 111, 95, 104, 97, 115, 104]), // "info_hash"
      info,
      SUITE_ID_2
    );

    // Key schedule context: mode || pskIdHash || infoHash
    const keyScheduleContext = new Uint8Array(1 + 32 + 32);
    keyScheduleContext.set(mode, 0);
    keyScheduleContext.set(new Uint8Array(pskIdHash), 1);
    keyScheduleContext.set(new Uint8Array(infoHash), 33);

    // Secret labeled IKM
    const secretLabeledIkm = buildWebCryptoLabeledIkm(
      LABEL_SECRET,
      psk, // empty
      SUITE_ID_2
    );

    // Derive key using @hpke/core method
    const keyLabeledInfo = buildWebCryptoLabeledInfo(
      new Uint8Array([107, 101, 121]), // "key"
      keyScheduleContext,
      SUITE_ID_2,
      32
    );
    const key = await webCryptoExtractAndExpand(
      kemSharedSecret.buffer as ArrayBuffer, // salt = KEM shared secret
      secretLabeledIkm.buffer as ArrayBuffer, // IKM
      keyLabeledInfo.buffer as ArrayBuffer, // info
      32
    );

    // Derive base nonce using @hpke/core method
    const nonceLabeledInfo = buildWebCryptoLabeledInfo(
      LABEL_BASE_NONCE,
      keyScheduleContext,
      SUITE_ID_2,
      12
    );
    const baseNonce = await webCryptoExtractAndExpand(
      kemSharedSecret.buffer as ArrayBuffer, // salt = KEM shared secret
      secretLabeledIkm.buffer as ArrayBuffer, // IKM
      nonceLabeledInfo.buffer as ArrayBuffer, // info
      12
    );

    const decryptedData = chachaDecrypt(
      ciphertextBuf,
      new Uint8Array(key),
      new Uint8Array(baseNonce),
      undefined
    );
    
    return decryptedData;
  } catch (error) {
    throw new Error(`Unable to perform hpkeChachaDecrypt: ${error} `);
  }
};

/**
 * React Native compatible decrypt function using ChaCha20-Poly1305
 * Accepts base64 encoded encrypted data and private key
 */
export const hpkeChachaDecryptRN = async (
  encryptedAuthKey: { encapsulated_key: string; ciphertext: string },
  recipientPrivateKeyBase64: string
): Promise<Uint8Array> => {
  try {
    // Convert base64 to Uint8Array
    const encappedKeyBuf = base64ToUint8Array(
      encryptedAuthKey.encapsulated_key
    );
    const ciphertextBuf = base64ToUint8Array(encryptedAuthKey.ciphertext);

    // Extract raw private key from PKCS#8 format
    const pkcs8Buffer = base64ToUint8Array(recipientPrivateKeyBase64);
    const rawPrivateKey = extractPrivateKeyFromPKCS8Bytes(pkcs8Buffer);
    const receiverPriv = uint8ArrayToHexString(rawPrivateKey);

    return await hpkeChachaDecrypt({
      ciphertextBuf,
      encappedKeyBuf,
      receiverPriv,
    });
  } catch (error) {
    throw new Error(`React Native HPKE ChaCha decrypt failed: ${error}`);
  }
};

/**
 * Generate a P-256 key pair
 */
export const generateP256KeyPair = (): KeyPair => {
  const privateKey = randomBytes(32);
  const publicKey = getPublicKey(privateKey, true);
  const publicKeyUncompressed = uint8ArrayToHexString(
    uncompressRawPublicKey(publicKey)
  );
  return {
    privateKey: uint8ArrayToHexString(privateKey),
    publicKey: uint8ArrayToHexString(publicKey),
    publicKeyUncompressed,
  };
};

/**
 * Create additional associated data (AAD) for ChaCha20-Poly1305 decryption
 * From Turnkey implementation
 */
export const buildAdditionalAssociatedData = (
  senderPubBuf: Uint8Array,
  receiverPubBuf: Uint8Array
): Uint8Array => {
  return new Uint8Array([
    ...Array.from(senderPubBuf),
    ...Array.from(receiverPubBuf),
  ]);
};

/**
 * Extract private key from PKCS#8 bytes (from Turnkey implementation)
 */
export const extractPrivateKeyFromPKCS8Bytes = (
  privateKey: Uint8Array
): Uint8Array => {
  return privateKey.slice(36, 36 + 32);
};

/**
 * Compress raw public key (from Turnkey implementation)
 */
export const compressRawPublicKey = (rawPublicKey: Uint8Array): Uint8Array => {
  const len = rawPublicKey.byteLength;
  var compressedBytes = rawPublicKey.slice(0, (1 + len) >>> 1);
  compressedBytes[0] = 0x02 | (rawPublicKey[len - 1]! & 0x01);
  return compressedBytes;
};

/**
 * Uncompress raw public key (from Turnkey implementation)
 */
export const uncompressRawPublicKey = (
  rawPublicKey: Uint8Array
): Uint8Array => {
  if (rawPublicKey.length !== 33) {
    throw new Error("failed to uncompress raw public key: invalid length");
  }

  if (!(rawPublicKey[0] === 2 || rawPublicKey[0] === 3)) {
    throw new Error("failed to uncompress raw public key: invalid prefix");
  }

  const lsb = rawPublicKey[0] === 3;
  const x = BigInt("0x" + uint8ArrayToHexString(rawPublicKey.subarray(1)));

  const p = BigInt(
    "115792089210356248762697446949407573530086143415290314195533631308867097853951"
  );
  const b = BigInt(
    "0x5ac635d8aa3a93e7b3ebbd55769886bc651d06b0cc53b0f63bce3c3e27d2604b "
  );
  const a = p - BigInt(3);

  const rhs = ((x * x + a) * x + b) % p;
  let y = modSqrt(rhs, p);
  if (lsb !== testBit(y, 0)) {
    y = (p - y) % p;
  }

  if (x < BigInt(0) || x >= p) {
    throw new Error("x is out of range");
  }

  if (y < BigInt(0) || y >= p) {
    throw new Error("y is out of range");
  }

  var uncompressedHexString = "04" + bigIntToHex(x, 64) + bigIntToHex(y, 64);
  return uint8ArrayFromHexString(uncompressedHexString);
};

// Helper functions (from Turnkey implementation)

const buildLabeledIkm = (
  label: Uint8Array,
  ikm: Uint8Array,
  suiteId: Uint8Array
): Uint8Array => {
  const combinedLength =
    HPKE_VERSION.length + suiteId.length + label.length + ikm.length;
  const ret = new Uint8Array(combinedLength);
  let offset = 0;

  ret.set(HPKE_VERSION, offset);
  offset += HPKE_VERSION.length;

  ret.set(suiteId, offset);
  offset += suiteId.length;

  ret.set(label, offset);
  offset += label.length;

  ret.set(ikm, offset);

  return ret;
};

// @hpke/core compatible helper functions
const buildHpkeCoreLabeledIkm = (
  label: Uint8Array,
  ikm: Uint8Array,
  suiteId: Uint8Array
): Uint8Array => {
  // @hpke/core format: "HPKE-v1" || suiteId || label || ikm
  const combinedLength =
    HPKE_VERSION.length + suiteId.length + label.length + ikm.length;
  const ret = new Uint8Array(combinedLength);
  let offset = 0;

  ret.set(HPKE_VERSION, offset);
  offset += HPKE_VERSION.length;

  ret.set(suiteId, offset);
  offset += suiteId.length;

  ret.set(label, offset);
  offset += label.length;

  ret.set(ikm, offset);

  return ret;
};

const buildHpkeCoreLabeledInfo = (
  label: Uint8Array,
  info: Uint8Array,
  suiteId: Uint8Array,
  len: number
): Uint8Array => {
  // @hpke/core format: i2osp(len, 2) || "HPKE-v1" || suiteId || label || info
  const ret = new Uint8Array(
    2 + HPKE_VERSION.length + suiteId.length + label.length + info.length
  );
  let offset = 0;

  // i2osp(len, 2) - encode length as 2 bytes big-endian
  ret.set(new Uint8Array([len >> 8, len & 0xff]), offset);
  offset += 2;

  ret.set(HPKE_VERSION, offset);
  offset += HPKE_VERSION.length;

  ret.set(suiteId, offset);
  offset += suiteId.length;

  ret.set(label, offset);
  offset += label.length;

  ret.set(info, offset);

  return ret;
};

// WebCrypto compatible functions matching @hpke/core exactly
const buildWebCryptoLabeledIkm = (
  label: Uint8Array,
  ikm: Uint8Array,
  suiteId: Uint8Array
): Uint8Array => {
  // @hpke/core format: HPKE_VERSION(7) + suiteId + label + ikm
  const ret = new Uint8Array(
    7 + suiteId.byteLength + label.byteLength + ikm.byteLength
  );
  ret.set(HPKE_VERSION, 0);
  ret.set(suiteId, 7);
  ret.set(label, 7 + suiteId.byteLength);
  ret.set(ikm, 7 + suiteId.byteLength + label.byteLength);
  return ret;
};

const buildWebCryptoLabeledInfo = (
  label: Uint8Array,
  info: Uint8Array,
  suiteId: Uint8Array,
  len: number
): Uint8Array => {
  // @hpke/core format: i2osp(len, 2) + HPKE_VERSION(7) + suiteId + label + info
  const ret = new Uint8Array(
    9 + suiteId.byteLength + label.byteLength + info.byteLength
  );
  ret.set(new Uint8Array([0, len]), 0);
  ret.set(HPKE_VERSION, 2);
  ret.set(suiteId, 9);
  ret.set(label, 9 + suiteId.byteLength);
  ret.set(info, 9 + suiteId.byteLength + label.byteLength);
  return ret;
};

const webCryptoExtractAndExpand = async (
  salt: ArrayBuffer,
  ikm: ArrayBuffer,
  info: ArrayBuffer,
  len: number
): Promise<ArrayBuffer> => {
  // React Native compatible implementation using @noble/hashes HKDF
  const saltBytes = new Uint8Array(salt);
  const ikmBytes = new Uint8Array(ikm);
  const infoBytes = new Uint8Array(info);

  const prk = hkdf.extract(sha256, ikmBytes, saltBytes);
  const result = hkdf.expand(sha256, prk, infoBytes, len);
  return (result.buffer as ArrayBuffer).slice(
    result.byteOffset,
    result.byteOffset + result.byteLength
  );
};

// @hpke/core labeledExtract - only does HKDF-Extract, not Extract+Expand
const webCryptoLabeledExtract = async (
  salt: ArrayBuffer,
  label: Uint8Array,
  ikm: Uint8Array,
  suiteId: Uint8Array
): Promise<ArrayBuffer> => {
  const labeledIkm = buildWebCryptoLabeledIkm(label, ikm, suiteId);

  // HKDF-Extract only using @noble/hashes
  const saltBytes =
    salt.byteLength === 0 ? new Uint8Array(32) : new Uint8Array(salt); // SHA-256 hash size
  const result = hkdf.extract(sha256, labeledIkm, saltBytes);
  return (result.buffer as ArrayBuffer).slice(
    result.byteOffset,
    result.byteOffset + result.byteLength
  );
};

const buildLabeledInfo = (
  label: Uint8Array,
  info: Uint8Array,
  suiteId: Uint8Array,
  len: number
): Uint8Array => {
  const suiteIdStartIndex = 9;
  const ret = new Uint8Array(
    suiteIdStartIndex + suiteId.byteLength + label.byteLength + info.byteLength
  );
  ret.set(new Uint8Array([0, len]), 0);
  ret.set(HPKE_VERSION, 2);
  ret.set(suiteId, suiteIdStartIndex);
  ret.set(label, suiteIdStartIndex + suiteId.byteLength);
  ret.set(info, suiteIdStartIndex + suiteId.byteLength + label.byteLength);
  return ret;
};

const extractAndExpand = (
  sharedSecret: Uint8Array,
  ikm: Uint8Array,
  info: Uint8Array,
  len: number
): Uint8Array => {
  const prk = hkdf.extract(sha256, ikm, sharedSecret);
  const resp = hkdf.expand(sha256, prk, info, len);
  return new Uint8Array(resp);
};

const deriveSS = (encappedKeyBuf: Uint8Array, priv: string): Uint8Array => {
  const ss = p256.getSharedSecret(
    uint8ArrayFromHexString(priv),
    encappedKeyBuf
  );
  return ss.slice(1);
};

const chachaEncrypt = (
  plainTextData: Uint8Array,
  key: Uint8Array,
  nonce: Uint8Array,
  aad?: Uint8Array
): Uint8Array => {
  const chacha = chacha20poly1305(key, nonce, aad);
  return chacha.encrypt(plainTextData);
};

const chachaDecrypt = (
  encryptedData: Uint8Array,
  key: Uint8Array,
  nonce: Uint8Array,
  aad?: Uint8Array
): Uint8Array => {
  const chacha = chacha20poly1305(key, nonce, aad);
  return chacha.decrypt(encryptedData);
};

const getKemContext = (
  encappedKeyBuf: Uint8Array,
  publicKey: string
): Uint8Array => {
  const encappedKeyArray = new Uint8Array(encappedKeyBuf);
  const publicKeyArray = uint8ArrayFromHexString(publicKey);

  const kemContext = new Uint8Array(
    encappedKeyArray.length + publicKeyArray.length
  );
  kemContext.set(encappedKeyArray);
  kemContext.set(publicKeyArray, encappedKeyArray.length);

  return kemContext;
};

const bigIntToHex = (num: bigint, length: number): string => {
  const hexString = num.toString(16);
  if (hexString.length > length) {
    throw new Error(
      `number cannot fit in a hex string of ${length} characters`
    );
  }
  return hexString.padStart(length, "0");
};

// React Native compatible base64 conversion
const base64ToUint8Array = (base64: string): Uint8Array => {
  const binaryString = atob(base64);
  const bytes = new Uint8Array(binaryString.length);
  for (let i = 0; i < binaryString.length; i++) {
    bytes[i] = binaryString.charCodeAt(i);
  }
  return bytes;
};

/**
 * Format HPKE Buffer Function
 * Returns a JSON string of an encrypted bundle
 */
export const formatHpkeBuf = (encryptedBuf: Uint8Array): string => {
  const compressedSenderBuf = encryptedBuf.slice(0, 33);
  const encryptedData = encryptedBuf.slice(33);

  const encappedKeyBufHex = uint8ArrayToHexString(
    uncompressRawPublicKey(compressedSenderBuf)
  );
  const ciphertextHex = uint8ArrayToHexString(encryptedData);

  return JSON.stringify({
    encappedPublic: encappedKeyBufHex,
    ciphertext: ciphertextHex,
  });
};
