import { AbstractBaseProvider } from './base-provider';
import { TurnkeyConfig, ProviderError, UniversalKeyPair, TurnkeyInitAuthRequest, InitAuthResponse, TurnkeyCompleteAuthRequest, CompleteAuthResponse, TurnkeySignRequest, TurnkeySignResponse, SignRequest, SendTransactionRequest, TurnkeySendRequest, CompleteAuthRequest } from '../types';
import { generateTurnkeyKeyPair, type TurnkeyKeyPair, signWithApiKey, decryptCredentials, getPublicKeyFromDecryptedData } from '../utils/crypto/turnkey-crypto';
import { TransactionResult } from '../managers/signing-manager';

export class TurnkeyProvider extends AbstractBaseProvider{
  private turnkeyConfig: TurnkeyConfig;

  constructor(config: TurnkeyConfig) {
    super('turnkey', config);
    this.turnkeyConfig = config;
  }

  async generateLocalKeyPair(): Promise<TurnkeyKeyPair> {
    try {
      const keypair = await generateTurnkeyKeyPair();
      return keypair;
    } catch (error: any) {
      throw new ProviderError(
        `Failed to generate local Turnkey key pair: ${error.message}`,
        'turnkey',
        undefined,
        error
      );
    }
  }

  /**
   * Generate a universal key pair (implements BaseAuthProvider interface)
   * This automatically uses Turnkey's P256 key generation
   */
  async generateUniversalKeyPair(): Promise<TurnkeyKeyPair> {
    const turnkeyKeys = await this.generateLocalKeyPair();
    
    return {
      publicKey: turnkeyKeys.publicKey,
      privateKey: turnkeyKeys.privateKey,
      publicKeyUncompressed: turnkeyKeys.publicKeyUncompressed,
        
    };
  }

  /**
   * Turnkey authentication (multi-step process)
   * Step 1: Initiate authentication with email and app details
   */
  async initiateAuth(request: TurnkeyInitAuthRequest): Promise<InitAuthResponse> {

    try {
      // API key is required for authentication operations
      if (!this.config.apiKey) {
        throw new ProviderError(
          'API key is required for Turnkey authentication. Provide apiKey in config for API operations.',
          'turnkey'
        );
      }

      // For Turnkey, we expect email to be provided
      if (!request.email) {
        throw new ProviderError(
          'Email is required for Turnkey authentication',
          'turnkey'
        );
      }

      // Use Grid API client
      const client = this.gridApiClient!;

      // Use provider-specific API method
      const response = await client.initAuthWithTurnkey(request);

      if (!response.success) {
        throw new ProviderError(
          response.error || 'Turnkey authentication failed',
          'turnkey',
          response.code ? parseInt(response.code) : undefined
        );
      }

      const res = response.data;
      const result = {
        data: {
          mpcPrimaryId: res.data.mpc_primary_id,
          otpId: res.data.otp_id,
        },
        metadata: response.data.metadata
      }
      return result;
    } catch (error: any) {
      if (error instanceof ProviderError) {
        throw error;
      }
      throw new ProviderError(
        `Turnkey authentication failed: ${error.message}`,
        'turnkey',
        undefined,
        error
      );
    }
  }

  async completeAuthAndCreateAccount(request: CompleteAuthRequest): Promise<any> {
    throw new ProviderError('Turnkey completeAuthAndCreateAccount not yet implemented', 'turnkey');
  }

  /**
   * Complete Turnkey authentication with OTP verification
   */
  async completeAuth(request: TurnkeyCompleteAuthRequest): Promise<CompleteAuthResponse> {
    try {
      // API key is required for authentication operations
      if (!this.config.apiKey) {
        throw new ProviderError(
          'API key is required for Turnkey authentication. Provide apiKey in config for API operations.',
          'turnkey'
        );
      }

      // Use Grid API client
      const client = this.gridApiClient!;

      // Use provider-specific API method  
      const response = await client.completeTurnkeyAuth(request);

      return response.data;
    } catch (error: any) {
      if (error instanceof ProviderError) {
        throw error;
      }
      throw new ProviderError(
        `Turnkey authentication failed: ${error.message}`,
        'turnkey',
        undefined,
        error
      );
    }
  }

  async sign(request: TurnkeySignRequest): Promise<string> {
    const { transaction, signers, credentials } = request;

    // Ensure we only have UniversalKeyPairs (Solana Keypairs should be auto-routed away)
    const firstSigner = signers[0];
    if (!firstSigner || !('privateKey' in firstSigner)) {
      throw new ProviderError(
        'Turnkey provider requires UniversalKeyPair format. Solana Keypairs should be auto-routed to Solana provider.',
        'turnkey'
      );
    }

    // TODO: check if multiple signers would work
    if(request.signers.length > 1) {
      throw new ProviderError('Turnkey provider does not support multiple signers', 'turnkey');
    }

    const universalSigner = firstSigner as UniversalKeyPair;
    const decryptedData = await decryptCredentials(credentials, universalSigner.privateKey);
    
    const publicKey = await getPublicKeyFromDecryptedData(decryptedData);

    const signature = await signWithApiKey({
        payload: JSON.stringify(transaction),
        privateKey: decryptedData,
    });

    const stamp = {
      publicKey,
      signature,
    };



    return JSON.stringify({
              requestParameters: transaction,
              stamp,
          });
  
  }

  async send(request: TransactionResult): Promise<any> {
    try {
      
      // const { address, transactionId, payload } = request;
  
      // const response = await this.gridApiClient.sendTransaction(address, transactionId, payload);
  
      // return response;
      return {};
    } catch (error: any) {
      throw new ProviderError(
        `Turnkey send failed: ${error.message}`,
        'turnkey',
        undefined,
        error
      );
    }
   
  }

  async signAndSend(request: SignRequest): Promise<any> {
    throw new ProviderError('Turnkey provider does not support signing and sending transactions', 'turnkey');
  }

}