import { ApiSpendingLimitPeriod, AuthorizationError, SpendingLimitPeriod } from "../types/common";

const gridToProgram: Record<SpendingLimitPeriod, string> = {
    one_time: "one_time",
    daily: "day",
    weekly: "week",
    monthly: "month",
};

const programToGrid: Record<ApiSpendingLimitPeriod, SpendingLimitPeriod> = {
    one_time: "one_time",
    day: "daily",
    week: "weekly",
    month: "monthly",
};

export function gridToProgramPeriod(period: SpendingLimitPeriod): ApiSpendingLimitPeriod {
    const mapped = gridToProgram[period];
    if (!mapped) throw new AuthorizationError("Invalid period", "INVALID_REQUEST");
    return mapped as ApiSpendingLimitPeriod;
}

export function programToGridPeriod(period: ApiSpendingLimitPeriod): SpendingLimitPeriod {
    const mapped = programToGrid[period];
    if (!mapped) throw new AuthorizationError("Invalid period", "INVALID_REQUEST");
    return mapped;
}