
// import { p256 } from "@noble/curves/p256";
import { createHash } from "sha256-uint8array";
import { uint8ArrayFromHexString, uint8ArrayToHexString} from '@turnkey/encoding';

export interface TurnkeyKeyPair {
  publicKey: string;
  privateKey: string;
  publicKeyUncompressed: string;
}


export const generateTurnkeyKeyPair = async (): Promise<TurnkeyKeyPair> => {
  
  try {
    // Dynamic import to avoid bundling issues
    const { generateP256KeyPair } = await import('@turnkey/crypto');

    try {
      const keyPair = generateP256KeyPair();
      return {
        publicKey: keyPair.publicKey,
        privateKey: keyPair.privateKey,
        publicKeyUncompressed: keyPair.publicKeyUncompressed
      };
    } catch (error) {
      throw new Error(`Failed to generate Turnkey key pair: ${error}`);
    }
    
    
  } catch (error) {
    throw new Error(`Failed to generate Turnkey key pair: ${error}`);
  }
}; 

export const decryptCredentials = async (credentials: string, privateKey: string): Promise<string> => {
  try {
    // Dynamic import to avoid bundling issues
    const { decryptCredentialBundle } = await import('@turnkey/crypto');

    try {
      const decryptedData = decryptCredentialBundle(credentials, privateKey);
      return decryptedData;
    } catch (error) {
      throw new Error(`Failed to decrypt credentials: ${error}`);
    }
    
    
  } catch (error) {
    throw new Error(`Failed to generate Turnkey key pair: ${error}`);
  }
}

export const getPublicKeyFromDecryptedData = async (data: string): Promise<string> => {
  const { getPublicKey } = await import('@turnkey/crypto');
  const publicKey = uint8ArrayToHexString(
    getPublicKey(uint8ArrayFromHexString(data), true)
  );  
  return publicKey;
}

export const signWithApiKey = async (input: {
  payload: string;
  privateKey: string;
}): Promise<string> => {
  const { p256 } = require('@noble/curves/p256');
  const hash = createHash().update(input.payload).digest();
  const signature = p256.sign(hash, input.privateKey);
  return signature.toDERHex();
};